.draggable-container {
  /* width: 200px;
  height: 200px; */
  top: "35%";
  left: "30%";
  transform: "translate(-50%, -50%)";
  background-color: #feb127;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  border-radius: 10px;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: opacity 0.3s ease-in-out;
  opacity: 1;
  cursor: grab;
  position: fixed;
}

.draggable-container.closed {
  opacity: 0;
}

.draggable-container:not(.closed) {
  cursor: grab;
}

.draggable-container:not(.closed):hover .draggable-button {
  cursor: grab;
}

.draggable-container.closed,
.draggable-container:not(.closed):hover {
  cursor: default;
}

/* SearchBar.css */
.search-bar {
  position: relative;
  width: 500px; /* Set your desired width */
  margin: 1px auto; /* Adjust the margin as needed */
}

input[type="text"] {
  margin: auto;
  width: 95%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid white;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
}

input[type="text"]:focus {
  border-color: balck; /* Change the color as per your preference */
  box-shadow: 0 0 5px #000000;
}
